import { mapState, mapActions, mapMutations } from 'vuex'
import {regionData, provinceAndCityDataPlus, regionDataPlus, CodeToText, TextToCode } from 'element-china-area-data'
export default {
  data() {
    return {
      currentPage: 1,//默认开始页面
      pagesize: 2,//每页的数据条数
      tableData: [],                      //  表格数据
      sea_customer_edit:{},//要修改的数据
      selectValue:'',//勾选的数据
      isShow:false,//不显示

      dialogTransferVisible: false,       //  转给他人
      dialogEditVisible: false,          //  编辑
      dialogMergeVisible: false,          //  合并
      dialogImportVisible: false,         //  导入
      dialogSettingsVisible: false,       //  规则设置

      folowForm: {},                      //  转给他人表单
      formMerge: {},                      //  合并客户表单
      formImport: {},                      //  导入客户表单
      options: regionData,                            //  地区总数据
      selectedOptions: [],                            //  地区结果
    }
  },

  mounted(){


    /**
     * 页面加载前获取所有公海客户数据
     */
    this.initCrmSeaCustomer = function(){
      this.$axios.post(this.$store.state.crmPath+"/crmSeaCustomerPR/queryCrmSeaCustomer").then(res => {
        this.tableData = res.data;
      })
    };
    this.initCrmSeaCustomer();


  },

  methods: {

    /**
     * 勾选数据点击编辑按钮
     */
    seaCustomerEdit(){
      var select_value = this.selectValue;
      //只能勾选一条数据
      if (select_value.length !=1) {
        this.$notify({ message: '请勾选一条数据！', position: 'bottom-right'});
        return;
      };

      //取勾选的数据
      this.sea_customer_edit = this.selectValue[0];
      //显示弹框
      this.dialogEditVisible = true;

    },

    /**
     * 保存的编辑的数据
     */
    seaCustomerSave(){

      let param = new URLSearchParams();
      const data = this.sea_customer_edit;
      param.append('data', JSON.stringify(data));

      this.$axios.post(this.$store.state.crmPath+"/crmSeaCustomerPR/updateCrmSeaCustomer",param).then(res => {
        this.$notify({ message: '操作成功！', position: 'bottom-right'});
        this.dialogEditVisible = false;
        this.initCrmSeaCustomer();
      })



    },

    /**
     * 删除公海客户
     */
    handleDel(){

      var select_value = this.selectValue;
      if (select_value.length < 1) {
        this.$notify({ message: '请勾选数据！', position: 'bottom-right'});
        return;
      };

      this.$confirm('确定删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let param = new URLSearchParams();
        const data = this.selectValue;
        param.append('data', JSON.stringify(data));
        var url = this.$store.state.crmPath+'/crmSeaCustomerPR/deleteCrmSeaCustomer';
        this.$axios.post(url, param).then(res => {
          this.initCrmSeaCustomer();
          this.$notify({ message: '操作成功！', position: 'bottom-right'});
        });
      });

    },

    /**
     * 勾选的数据
     */
    handleSelectionChange(val) {
      this.selectValue = val;
    },


    /**
     * 分页
     */
    handleCurrentChange(val){
      this.currentPage = val;
    },


    //  合并客户
    mergeChange(value){
      console.log(value);
      console.log(this.formMerge.id);
    },
  }
}



